import { LowSync } from 'lowdb'
// import {} from './gsheetsApi'
import GSheetsAdapter from './GSheetsAdapter'


export class Database {
    constructor(adapter) {
        this.adapter = adapter
        this.db = new LowSync(adapter)
        this.isReady = false

        this.makeReady = this.makeReady.bind(this)

        this.db.read().then(this.makeReady)
    }

    makeReady() {
        if (!this.db.data) {
            console.log("Making ready...")
            // this.db.data
        }

        this.isReady = true
    }


}
console.log("adfaf")

export default (new Database(new GSheetsAdapter()))